body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(#002a30),
    color-stop(#203a43),
    to(#0f2027)
  );
  background: -webkit-linear-gradient(
    right,
    #002a30,
    #203a43,
    #0f2027
  );
  background: linear-gradient(
    to left,
    #002a30,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.avatar-img {
  height: 250px;
  width: auto;
  padding: 4em 1em 1em 1em;
  border-radius: 80%;
  -webkit-transition: all 2s;
  transition: all 2s;
}

.avatar-img:hover {
  /* transform: scale(2); */
  background-color: rgba(43, 43, 43, 0.342);
  box-shadow: 0 5px 15px #04ff10;
}

.banner-text {
  background-color: black;
  width: 75%;
  opacity: 0.6;
  margin: auto;
  border-radius: 20%;
  padding: 0.6em;
  font-weight: bold;
  color: white;
  -webkit-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}
.banner-text:hover {
  box-shadow: 0 5px 10px #04ff10;
  opacity: 1;
  color: #04ff10;
}

.social-links {
  /* display: block; */
  /* justify-content: space-evenly; */
  width: 75%;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.63);
  opacity: 0.6;
  border-radius: 10%;
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
}

.social-links:hover {
  box-shadow: 0 5px 10px #04ff10;
  opacity: 1;
}
.social-links svg {
  font-size: 3em;
  margin: 1rem;
}

.projects-grid {
  display: flex;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 60%;
  height: 550px;
  background: rgba(235, 233, 233, 0.575);
  color: black;
  position: relative;
  top: 2.3em;
}

.contact-grid h2 {
  font-family: "Anton", sans-serif;
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 56px;
  padding-right: 5rem;
  margin-top: 1.5rem;
  position: relative;
  top: 12px;
}
