/* ABOUT ME PAGE CSS */

.tabHeading {
  font-family: monospace;
  color: rgb(247, 247, 247);
  font-size: 1.8rem;
}

/* CONTACT FORM CSS */

.contactUs {
  background-color: #080c0057;
  color: snow;
  border: #27221f solid 1px;
  font-family: work sans, helvetica, tahoma, geneva, arial, sans-serif;
  height: 95.5%;
  padding: 1rem;
}

.contactUs h3 {
  margin-bottom: 4rem;
  font-family: sans, helvetica, tahoma, geneva, arial, sans-serif;
  color: snow;
}

.form-group label {
  font-family: sans, helvetica, tahoma, geneva, arial, sans-serif;
  color: snow;
}

.form-group Textarea {
  background-color: rgba(0, 3, 3, 0.384);
  color: rgb(0, 255, 64);
  width: 100%;
  height: 8rem;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1rem;
  font-weight: 800;
}

#contact-form Button {
  background-color: rgba(0, 3, 3, 0.384);
  color: rgb(255, 255, 255);
  border: 1px solid rgb(255, 255, 255);
  margin-top: 2vh;
  -webkit-columns: 20;
     -moz-columns: 20;
          columns: 20;
  -webkit-transition: all 2s;
  transition: all 2s;
}
#contact-form Button:hover {
  background-color: #000000;
  color:   rgb(10, 245, 41);
  border: 1px solid   rgb(0, 0, 0);
  /* box-shadow: 0 3px 5px  rgb(10, 245, 41); */
}

input[type="text"],
input[type="email"] {
  font-family: 'Courier New', Courier, monospace;
  font-size: 1rem;
  font-weight: 800;
  color: rgb(0, 255, 64);
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  background-color: rgba(0, 3, 3, 0.384);
}



/* CONTACT INFORMATION COMPONENT */

.info-list {
  background-color: #080c0057;
  color: rgb(241, 236, 225);
  /* text-align: left; */
  border: #27221f solid 1px;
  height: 95.5%;
  font-size: 1.8rem;
  padding: 3rem;
  padding-left: 2rem;
  font: 2vh monospace !important;
}

.typewriter {
  font-family: "Courier New", Courier, monospace;
  display: inline-block;
}

.typewriter-text {
  display: inline-block;
  color: rgb(255, 254, 254);
  background-color: #00000079;
  font-family: monospace; /* Web-safe typewriter-like font */
  font-size: 1rem;

  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid rgb(238, 255, 0); /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  -webkit-animation: typing 5s steps(30, end), blinking-cursor 1s step-end infinite;
          animation: typing 5s steps(30, end), blinking-cursor 1s step-end infinite;
}

/* The typing effect */

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}
/* The typewriter cursor effect */
@-webkit-keyframes blinking-cursor {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}
@keyframes blinking-cursor {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

