@media all and (max-width: 480px) {
  /* LANDING - PAGE */

  .avatar-img {
    height: 250px;
    width: auto;
    padding: 8em 1em 1em 2em;
    border-radius: 80%;
    margin-top: -6rem;
  }

  .banner-text {
    background-color: black;
    width: 100%;
    opacity: 0.6;
    margin: auto;
    border-radius: 20%;
    color: white;
  }
  .social-links {
    width: 100%;
    margin: -2rem 0 0 0;
    background-color: rgba(0, 0, 0, 0.63);
    opacity: 0.6;
    border-radius: 10%;
  }
  .typewriter-text {
    background-color: rgba(241, 200, 124, 0);
    font-size: .5rem;
    overflow: auto; /* Ensures the content is not revealed until the animation */
    white-space: nowrap; /* Keeps the content on a single line */
    letter-spacing: 0.1em; /* Adjust as needed */
  }
 
  /* ABOUT-ME PAGE */
  .contactUs {
    background-color: #051318a8;
  }

  /* PROJECTS PAGE */

  .mdl-tabs__tab-bar {
    color: rgb(145, 1, 1);
    background-color: #ff0000
  }

  Card {
    /* display: flex; */
    width: 2rem;
    height: 2rem;
  }
  
  CardActions a {
    display: inline;
  }
  CardActions a OverlayTrigger i {
    font-size: 1rem;

  }
}

